.buttons__button-container {
  position: absolute;
  top: 88vh;
  height: 11vh;
  width: 100vw;
  max-width: 895px;
  //   margin-left: 2vw;
  padding: 1vh 7vw;
  display: flex;
  justify-content: space-around;
}

.buttons__btn {
  border-radius: 50%;
  height: 55px;
  width: 55px;
  display: flex;
  justify-content: center;
  background-color: lighten($light-grey, $amount: 15);
  border: none;
  -webkit-box-shadow: 0px 0px 13px -4px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 13px -4px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 13px -4px rgba(0, 0, 0, 0.75);
}

.buttons__icon-thumbs-up {
  margin-top: 13px;
  font-size: 25px;
  color: #55d6fe;
  &.animate {
    animation: tumbing-up 0.7s;
  }
}

@keyframes tumbing-up {
  from {
    text-shadow: 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw;
  }
  to {
    text-shadow: -20vw -20vw, 0vw -30vw, 20vw -20vw, 25vw 0vw, -25vw 0vh;
    color: transparent;
  }
}

.buttons__icon-heart {
  margin-top: 10px;
  font-size: 35px;
  color: #fc6269;
  animation: beating-heart 7s ease-in 0s infinite alternate forwards;

  &.animate {
    animation: heart-attack 1s 3;
  }
}

@keyframes heart-attack {
  from {
    text-shadow: 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw,
      0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw, 0vw 0vw;
  }
  to {
    text-shadow: -20vw -20vw, 0vw -30vw, 20vw -20vw, 25vw 0vw, -25vw 0vw,
      -40vw -50vh, -20vw -58vh, 0vw -61vh, 40vw -50vh, 20vw -58vh, -40vw -70vh,
      -20vw -78vh, 0vw -82vh, 40vw -70vh, 20vw -78vh;
    color: transparent;
  }
}

.buttons__icon-next {
  margin-top: 11px !important;
  font-size: 30px !important;
  color: #a4a1ae;
}

.buttons__icon-next {
  margin-top: 9px;
  font-size: 25px;
  color: #a4a1ae;
}

@keyframes beating-heart {
  0% {
    transform: scaleX(1) rotate(0deg);
    -o-transform: scaleX(1) rotate(0deg);
  }

  97% {
    transform: scaleX(1) rotate(0deg);
    -o-transform: scaleX(1) rotate(0deg);
  }

  98% {
    transform: scaleX(1.3) scaleY(1.3);
    -o-transform: scaleX(1);
  }
  99% {
    transform: scaleX(1.3) scaleY(1.3);
    -o-transform: scaleX(1);
  }
  100% {
    transform: scaleX(1);
    -o-transform: scaleX(1);
  }
}
